$col-blue:hsl(196, 18%, 33%);
$col-green:hsl(56, 18%, 33%);
$col-yellow:hsl(41, 34%, 50%);
$col-orange:hsl(28, 69%, 38%);
$col-red:hsl(9, 58%, 35%);

