#app-frame{

	--person-a-x:0;
	--person-a-y:0;
	--person-b-x:0;
	--person-b-y:0;

	// shadow
	--wig:0;
	--hue:180;
	--l:50%;
	
	// we only disable this in jumbo mode
	position: relative;

	// height: 100%;
	// min-height: 320px;
	// max-height: 100vh;
	// max-width: 100%;
	// max-height: 100%;

	// object-fit: contain;

	transition:opacity 300ms ease-out;

	//margin:auto;
	display:block;

	box-shadow:
		var(--wig) -1.2px 1.2px hsla(var(--hue), 90%, var(--l), 0.059),
		var(--wig) -2.7px 2.7px hsla(var(--hue), 90%, var(--l), 0.074),
		var(--wig) -4.8px 4.9px hsla(var(--hue), 90%, var(--l), 0.083),
		var(--wig) -8px 8.2px hsla(var(--hue), 90%, var(--l), 0.09),
		var(--wig) -13.2px 13.5px hsla(var(--hue), 90%, var(--l), 0.098),
		var(--wig) -23.1px 23.6px hsla(var(--hue), 90%, 10%, 0.112),
		var(--wig) -50px 51px hsla(var(--hue), 90%, 10%, 0.15),
		
		var(--wig) 1.2px 1.2px hsla(var(--hue), 90%, var(--l), 0.059),
		var(--wig) 2.7px 2.7px hsla(var(--hue), 90%, var(--l), 0.074),
		var(--wig) 4.8px 4.9px hsla(var(--hue), 90%, var(--l), 0.083),
		var(--wig) 8px 8.2px hsla(var(--hue), 90%, var(--l), 0.09),
		var(--wig) 13.2px 13.5px hsla(var(--hue), 90%, var(--l), 0.098),
		var(--wig) 23.1px 23.6px hsla(var(--hue), 90%, 10%, 0.112),
		var(--wig) 50px 51px hsla(var(--hue), 90%, 10%, 0.15);
	// $col-yellow
	
	// Overlaid logo
	&:before{
		opacity:0.5;
		position: absolute;
		top:-4px;
		right:5px;
		width:10%;
		display: block;
		content:url(./assets/logos/interface_colour.svg);
	}

	// show a graphic but hide contents
	&::before, &::after{
		transition: 400ms linear opacity;
		opacity:0;
	}
	
	video{
		// rounded corners
		border-radius:var(--curve);
		overflow: hidden;

		// flip it around
		transform: scaleX(-1);

		// stretch it to flood the width
		// width:100vw;
		height: auto;
		//width:100%;

		// prevent overflow
		// max-width: 100vw;
		// max-height: 100vh;

		// video actually always maintains apect ratio which we can abuse...	
		@media (orientation: landscape) {
			height: 100%;
			width:calc(var(--width) / var(--height)  * 100vh );
		}
		@media (orientation: portrait) {
			width: 100%;
			height:calc(var(--height) / var(--width) * 100vw );
		}
		
		// @media (orientation: landscape) {
		// 	height: 100vh;
		// 	width:calc(var(--width) / var(--height)  * 100vh );
		// }
		// @media (orientation: portrait) {
		// 	width: 100vw;
		// 	height:calc(var(--height) / var(--width) * 100vw );
		// }
		
		// scale to fit screen?
		// landscape
		// @media (orientation: landscape) {
		// 	height:100vh;
		// 	width:calc(var(--width) / var(--height)  * 100vh );
		// }
		// // portrait
		// @media (orientation: portrait) {
		// 	width:100vw;
		// 	height:calc(var(--height) / var(--width) * 100vw );
		// }
	}

	video,canvas{
		// max-width:100%;
		// max-height:100vh;
		// height:auto;

		// put them all in the same place in the grid
		object-fit: contain;
		//margin:auto;
		visibility: hidden;	
	}

	// in front
	canvas{
		z-index: 1;
		position:absolute;
		top:0;
		left:0;
		// right:0;
		// bottom:0;
		width:100%;
		height:100%;
	}
}


