#toast{
	position: absolute;
	left:var(--pad);
	bottom:calc( 88px + var(--pad) );
	z-index:909;
	color:$col-dark;
	background-color:$col-white;
	border-radius: 3px;
	text-align: left;
	text-shadow : rgba(255,255,255,0.96) 0 -1px 2px,
					rgba($col-dark, 0.96) 0 0 2px;
	&:empty{
		display:none;
	}

	&:after{
		display: inline-block;
		content:'';
		width: 6px;
		height:calc(1rem - 0px);
		background-color:$col-orange;
		margin-left:calc(var(--pad)/2);
		animation: Blink 950ms 0s infinite normal both;
		vertical-align:text-bottom;
	}
}


#feedback{
	box-sizing: border-box;
	position: fixed;
	z-index: 303;
	top:var(--pad);
	text-align: center;
	// left:var(--pad);
	// right:var(--pad);
	// left:var(--pad);
	max-width: calc(100% - 2 * var(--pad));
	background-color:rgba($col-dark,0.9);
	text-shadow : rgba(255,255,255,0.96) 0 -1px 2px,
					rgba($col-dark, 0.96) 0 0 2px;
	
	// cool carat
	&::after{
		content:'';
		width: 1rem;
		height:100%;
		background-color: $col-yellow;
		display: inline-block;
	}
	&:empty{
		display: none;
	}
}

#feedback:hover ~ canvas{
	opacity:0.5;
}

