// BUTTON ABSTRACT
button,
#shared-controls > *{

	--icon-size:32px;
	--fade-speed:45ms;

	cursor: pointer;
	min-height:44px;
	position: relative;
	box-sizing: border-box;
	margin:0;
	padding:var(--pad);
	user-select: none;
	appearance: none;
	line-height: 1;
	// -0.5385rem + 7.6923vw
	// clamp(minimum, preferred, maximum)
	font-size:clamp(8px, -0.5385rem + 2.2vmax, 14px);

	// max-width:calc(100% - 2 * var(--pad));
	z-index:1;
	display: flex;
	//flex-wrap: auto;
	flex-direction: row;
	// ensure a gap between row items...

	// align-items: center;
		// justify-content: center;
		// flex-wrap: wrap;	
		

	align-items: center;
	// justify-content: space-between;
	justify-content: center;

	background-repeat:no-repeat;
	background-size:auto var(--icon-size);

	// background-position:top var(--pad) center;
	background-position: center left var(--pad);

	color:$col-dark;
	background-color:$col-cream;
	
	&:empty{ 
		display:none;
	}
	
	// Inherit the icon and recolour
	&:before{
		display: block;

		// try and set to the size of the background icon
		min-width:var(--icon-size);
		min-height:var(--icon-size);

		content:'';
		background:inherit;
		background-color:transparent;
		background-position:top center;

		filter:brightness(0%);
		
		opacity:1;
		// z-index: -1;
		transition:opacity var(--fade-speed) ease-out;
	}
	&:not(.hide-text):before{
		margin-right:calc(var(--pad) / 2 );
	}
	&:hover, &:focus{

		&:before{
			opacity:0;
			z-index:1;
		}
	}

	&.hide{
		display: none;
	}


	// show even the hidden text!
	@media screen and (max-width:1960px){
		&.hide-text{
			// %image-replacement
			text-indent: -9999px;
			overflow:hidden;
			color:transparent;
			text-shadow:none;
			white-space: pre;
			font-size: 0;
			background-position: center center;
			justify-content: center;
		}
	}


	// stack
	@media screen and (max-width:1240px){
		// --icon-size:26px;
		flex-direction: column;
		background-position:top var(--pad) center;
		
		&:not(.hide-text):before{
			margin-right:0;
		}
	}
	
	// smallest always hides text so overrides here...
	@media screen and (max-width:460px){
		--icon-size:22px;
		font-size: 0;
		text-indent: -9999px;
		overflow:hidden;
		color:transparent;
		flex-direction: row;
		// as there is now no loner any text we need to centralise the icon
		background-position: center center;
		// justify-content: center;
		// &:before{
		// 	margin-right:0;
		// }
	}

}



// Overlay buttons
#shared-controls{

	// backgrounds
	--col-regular:#{$col-dark};
	--col-hover:#{$col-orange};
	--col-select:#{$col-yellow};
	
	bottom:0;
	// position:fixed;
	left:0;
	//right:0;
	min-width:100vw;

	display:flex;
	align-items: stretch;
	justify-content: stretch;
	border-top:1px dotted var(--col-regular);
	//overflow: hidden;
	// overflow: scroll;
	overflow-y:hidden;

	background-image:linear-gradient($col-grey, $col-cream);
	
	// need to bring it into z-range
	z-index:303;
	
	// @media screen and (min-width:640px){
		
	// }

	// we swap this to absolute when we want to window it
	position: fixed;

	label{

		// background-color:$col-yellow;
		// base plane shows funky
		
		input{
			opacity:0;
			height: 0;
			width: 0;
			overflow: hidden;
			position: absolute;
			top:0;
			left:0;
		}	
	
		// Indicator
		&:after{
			
			background-color: var(--col-select, $col-orange);
			opacity:0.5;
			content:'';
			display: block;
			position: absolute;
			left:0;
			right:0;
			bottom:0;
			height:8px;
			transform-origin: center bottom;
			transform:translateY(100%);
			transition:opacity 400ms linear,
